/* Navbar.css */
.navbar {
    background-color: #333;
    padding: 1rem;
    display: flex;
    align-items: center;
  }
  
  .navbar-links {
    list-style: none;
    display: flex;
    gap: 1rem;
  }
  
  .navbar-links li {
    display: inline;
  }
  
  .navbar-links a {
    color: white;
    text-decoration: none;
  }
  
  .navbar-links a:hover {
    text-decoration: underline;
  }
  
  .language-switch {
    margin-left: auto;
    display: flex;
    gap: 0.5rem;
  }
  
  .language-switch button {
    background-color: transparent;
    color: white;
    border: none;
    padding: 0.5rem;
    cursor: pointer;
  }
  
  .language-switch button:hover {
    text-decoration: underline;
  }
  
  .language-switch .active {
    text-decoration: underline;
    font-weight: bold;
  }
  