.App {
  text-align: center;
}

/* Ensure the app container fills the viewport height */
.app-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

/* Content container takes up remaining space */
.content-container {
  flex: 1;
  padding: 2vh 4vw;
  margin: 5vh 8vw;
}

/* Footer styles - already imported from Footer.css if needed */
.footer {
  position: relative;
  z-index: 2;
  background-color: #333;
  color: white;
  padding: 20px 0;
  text-align: center;
  margin-top: auto;
}

.footer a {
  color: #EFC06A;
  text-decoration: none;
  margin: 0px;
}

.footer a:hover {
  text-decoration: underline;
  color: #e0b160;
}

.footer-columns {
  text-align: left;
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
}

.footer-column {
  flex: 1;
  max-width: 300px;
  margin: 10px;
}

@media (max-width: 768px) {
  .footer-columns {
    flex-direction: column;
  }

  .footer-column {
    margin: 10px 0;
  }
}
