.members-page {
    margin: 20px;
    text-align: left;
  }
  
  .members-list {
    list-style: none;
    padding: 0;
  }
  
  .member-item {
    display: flex;
    align-items: center;
    gap: 15px;
    padding: 10px 0;
    border-bottom: 1px solid #ccc;
  }
  
  .member-image {
    width: 80px;
    height: 80px;
    object-fit: cover;
    border-radius: 50%;
  }
  
  .view-profile-button {
    margin-top: 10px;
  }
  