/* src/views/EventsPage.css */
.events-page {
    margin: 20px;
    text-align: left;
  }
  
  .events-list {
    list-style: none;
    padding: 0;
  }
  
  .event-item {
    padding: 10px 0;
    border-bottom: 1px solid #ccc;
  }
  
  .pagination {
    display: flex;
    justify-content: center;
    gap: 10px;
    margin-top: 20px;
  }
  
  .pagination button {
    padding: 5px 10px;
    cursor: pointer;
  }
  