.event-section {
    margin: 20px;
    text-align: center;
  }
  
  .event-list {
    display: flex;
    justify-content: space-around;
    gap: 20px;
    margin-top: 20px;
  }
  
  .event-item {
    width: 30%;
    border: 1px solid #ccc;
    padding: 10px;
    border-radius: 8px;
    text-align: left;
  }
  
  .event-image {
    width: 100%;
    height: auto;
    border-radius: 4px;
  }
  
  .event-info h3 {
    margin: 10px 0;
  }
  
  .show-more-button {
    margin-top: 20px;
    padding: 10px 20px;
    cursor: pointer;
    border: none;
    background-color: #333;
    color: white;
    border-radius: 4px;
  }
  
  .show-more-button:hover {
    background-color: #555;
  }
  