/* Adjusted Latest News Section to match Event styling */

.latest-news-section {
  margin: 20px;
  text-align: center;
}

.latest-news-container {
  display: flex;
  justify-content: space-around;
  gap: 20px;
  margin-top: 20px;
}

.news-item {
  width: 30%;
  border: 1px solid #ccc;
  padding: 10px;
  border-radius: 8px;
  text-align: left;
  background-color: #fff;
}

.news-image {
  width: 100%;
  height: auto;
  border-radius: 4px;
}

.news-info h3 {
  margin: 10px 0;
  font-size: 1.2em;
  color: #333;
}

.news-info p {
  color: #666;
  font-size: 0.9em;
}

.read-more-button {
  margin-top: 10px;
  padding: 10px 20px;
  cursor: pointer;
  border: none;
  background-color: #333;
  color: white;
  border-radius: 4px;
  text-align: center;
}

.read-more-button:hover {
  background-color: #555;
}

.view-all-news-button {
  margin-top: 20px;
  padding: 10px 20px;
  cursor: pointer;
  border: none;
  background-color: #333;
  color: white;
  border-radius: 4px;
}

.view-all-news-button:hover {
  background-color: #555;
}
